export default {
    data() {
        return {
            
        }
    },
    beforeCreate(){
        this.$store.dispatch('profitlist')
    },
    computed: {
        profitlist(){
            return this.$store.state.profitlist
        }
    },
    created(){
        document.title = '收益'
        this.$store.commit('ACTIVE',3)
    },
    methods: {
        details(id) {
            this.$router.push({path:'/ProfitDetails',query:{shopId: id}})
        }
    },
}



